import { IonSpinner } from '@ionic/react'
import { LatLngLiteral } from 'leaflet'
import { observer } from 'mobx-react-lite'
import { SnapshotOut } from 'mobx-state-tree'
import { OneSignal as OneSignalCapacitor } from '@ionic-native/onesignal'
import React, { useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'
import { PageApp } from '../../components'
import { SubHeader } from '../../components/SubHeader'
import BarraEstadoTransportista from '../../components/Transportista/BarraEstadoTransportista'
import { VerMapaIcono } from '../../components/Transportista/IconosTransportista'
import { RUTAS_APP } from '../../constants'
import { string2leaflet_location } from '../../helpers'
import { useApiToaster, useConfirm, useToaster } from '../../helpers-ts'
import { centroItinerarios } from '../../helpers-ts/helpers-leaflet'
import { useStores } from '../../models/root-store/root-store-context'
import HojaRutaModel from '../../models/types/HojaRutaModel'
import { GenericResponse, HojaRutaApi, ItinerarioApi, Vehiculo } from '../../services/api'
import './OportunidadesTransportista.css'

export const OportunidadesTransportista: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { transportistaStore, loginStore } = rootStore
  const [Oportunidades, setOportunidades] = useState([] as HojaRutaApi[])
  const [Ready, setReady] = useState(false)
  const [Location, setLocation] = useState<LatLngLiteral | null>(null)
  const [Center, setCenter] = useState<LatLngLiteral | null>(null)
  const [Polylines, setPolyline] = useState<LatLngLiteral[] | null>()
  const [Loading, setLoading] = useState(true)
  const [DetalleOportunidad, setDetalleOportunidad] = useState<SnapshotOut<typeof HojaRutaModel>>()
  const [VehiculoSeleccionado, setVehiculoSeleccionado] = useState<Vehiculo | null>()
  const apiToaster = useApiToaster()
  const addToast = useToaster()
  const IntervalID = React.useRef(null as any)
  const [PathInicial, setPathInicial] = useState(window.location.pathname)

  React.useEffect(() => {
    let currentUrlParams = new URLSearchParams(window.location.search)
    // Escuchamos el parametro r para actualizar el listado (cuando llega un push de nueva oportunidad)
    if (currentUrlParams.get('r')) {
      setLoading(true)
      obtenerOportunidades()
    }
  }, [window.location.search])

  React.useEffect(() => {
    IntervalID.current = setInterval(() => {
      if (PathInicial !== rootStore.currentPath) {
        return
      }
      obtenerOportunidades()
    }, 10000)
    obtenerOportunidades()
    return () => {
      clearInterval(IntervalID.current)
    }
  }, [])

  React.useEffect(() => {
    setVehiculoSeleccionado(transportistaStore.vehiculoSeleccionado as any)
  }, [transportistaStore.vehiculoSeleccionado])

  React.useEffect(() => {
    if (transportistaStore.oportunidadDetalle) {
      setDetalleOportunidad(transportistaStore.oportunidadDetalle)
    }
  }, [transportistaStore.oportunidadDetalle])

  React.useEffect(() => {
    setLocation(transportistaStore.lastLoc)
    if (!DetalleOportunidad) return
    let convertidos = DetalleOportunidad.itinerarios.map((i: ItinerarioApi) => string2leaflet_location(i.ubicacion))

    if (transportistaStore.lastLoc) {
      convertidos.splice(0, 0, transportistaStore.lastLoc)
    }

    setCenter(centroItinerarios(convertidos))
    setPolyline(convertidos)
    setTimeout(() => {
      setReady(true)
    }, 700)
  }, [DetalleOportunidad, transportistaStore.lastLoc])

  const obtenerOportunidades = () => {
    rootStore.environment.api.oportunidades().then((result) => {
      if (result.kind === 'ok') {
        setOportunidades(result.data)
      }
      setLoading(false)
    })
  }

  const [mostrarDialogo2, ConfirmDialog2] = useConfirm(
    () => {
      if (!DetalleOportunidad || !VehiculoSeleccionado) return

      setLoading(true)
      rootStore.environment.api
        .aceptarRuta(DetalleOportunidad.id)
        .then((result) => {
          setLoading(false)
          apiToaster(result, 'Oportunidad aceptada', 'aceptar la oportunidad')
          addToast('Ruta aceptada con exito!')
        })
        .then(() => {
          rootStore.environment.api
            .empezarRuta(DetalleOportunidad, VehiculoSeleccionado)
            .then((result: GenericResponse) => {
              setLoading(false)
              apiToaster(result, 'Ruta iniciada!', 'empezar la ruta')
              if (result.kind === 'ok') {
                // @ts-ignore
                history.push({ pathname: RUTAS_APP.HOME, cerrarMenu: true })
              }
            })
        })
    },
    () => {},
    '',
    '',
    true,
    '#ff6f33',
    'btn-outline-orange',
  )

  return (
    <PageApp
      preset={'p-driver'}
      subBarraExternaHeader={<SubHeader links={[{ titulo: 'Oportunidades' }]} />}
      sub_titulo={` `}
      mostrar_barra={false}
    >
      {ConfirmDialog2}
      <div id="oportunidades-page-content">
        <div className={'py-2'}>
          <h4 style={{ textAlign: 'center' }}>Oportunidades de entregas</h4>
          {Loading && (
            <div className="d-flex align-items-center justify-content-center mt-3">
              <IonSpinner name="crescent" className={'loadingSpinner'} />{' '}
            </div>
          )}
          {!Loading &&
            Oportunidades.map((item: HojaRutaApi) => {
              return (
                <div className={'oportunidades-list-item container-sm pt-3'} key={'oportunidad-' + item.id}>
                  <div className="datos-empresa" style={{ lineHeight: '1rem' }}>
                    <strong className='wrap' style={{maxWidth: '71vw'}}>
                      {item.inicio.fabricante}
                      {item.inicio.direccion_itinerario?.nombre && ', '}
                      {item.inicio.direccion_itinerario?.nombre}
                    </strong>
                    <p>
                      {item.inicio.direccion_itinerario?.calle_principal} N॰ {item.inicio.direccion_itinerario?.numero}
                    </p>
                    <div className="datos-punto-itinerario">
                      <img src={'assets/icon/oportunidades.png'} />
                      <strong>Punto de retiro: </strong>
                      <span>
                        {item.inicio.tiempo_en_transito + item.inicio.tiempo_parada_calculado} min -{' '}
                        {item.inicio.distancia} km
                      </span>
                      <br />
                      <img src={'assets/icon/arrival_flag.png'} />
                      <strong>Punto de entrega: </strong>
                      <span>
                        {item.fin.tiempo_en_transito + item.fin.tiempo_parada_calculado} min - {item.fin.distancia} km
                      </span>
                    </div>
                  </div>
                  <div>
                    <div
                      onClick={() => {
                        if (!transportistaStore.hojaRutaActiva) {
                          let currentUrlParams = new URLSearchParams(window.location.search)
                          const oportunidad = currentUrlParams.set('oportunidad', String(item.id))
                          history.push(RUTAS_APP.HOME + '?' + currentUrlParams.toString())
                        } else {
                          addToast('Finalice la entrega para ver nuevas oportunidades.')
                        }
                      }}
                    >
                      <VerMapaIcono />
                    </div>
                  </div>
                </div>
              )
            })}
          {!Loading && Oportunidades.length === 0 && (
            <div className={'d-flex flex-column h-100 justify-content-center align-items-center'}>
              <span className="text-center">
                No se encontraron <br />
                oportunidades disponibles
              </span>
            </div>
          )}
        </div>
      </div>
    </PageApp>
  )
})

export default OportunidadesTransportista
